import React from 'react';
import PropTypes from 'prop-types';
import Link, { navigateTo } from 'gatsby-link';
import Img from 'gatsby-image';
import { withStyles } from 'material-ui/styles';
import Button from 'material-ui/Button';
import Card, { CardActions, CardContent, CardHeader } from 'material-ui/Card';
import Typography from 'material-ui/Typography';

import TagArray from '../tag-array';

const propTypes = {

};

const styles = theme => ({
  root: {
    textAlign: 'left',
    color: theme.palette.secondary.dark,
    marginTop: 10,
    transition: `all ${(theme.transitions.duration.standard * 0.001).toFixed(2)}s ${theme.transitions.easing.easeInOut}`,
    borderRadius: 2,
    '&:hover': {
      marginTop: 8,
      marginBottom: 2,
      boxShadow: theme.shadows[3],
      cursor: 'pointer',
    },
  },
  a: {
    textDecoration: 'none',
    '& h2': {
      color: theme.palette.primary[900],
      borderBottom: `3px solid ${theme.palette.primary[600]}`,
      paddingBottom: '2px',
      '&:hover': {
        color: theme.palette.primary[300],
        borderBottom: `5px solid ${theme.palette.primary[300]}`,
        paddingBottom: '0',
      },
    },
  },
  flexBody: {
    display: 'flex',
    flexWrap: 'wrap',
    // padding: '0.5em',
  },
  cover: {
    maxWidth: 412,
    minWidth: 300,
    flexGrow: 2,
  },
  excerpt: {
    maxWidth: 412,
    // minWidth: 250,
    flexGrow: 2,
    display: 'flex',
    flexWrap: 'wrap',
    alignContent: 'space-between',
  },
  title: {
    margin: '0.5em',
  },
  contentBody: {
    flexGrow: 2,
  },
  tags: {
    display: 'block',
    flexGrow: 1,
  },
});

const PostExcerpt = (props) => {
  const { classes, cover, excerpt, html, path, title, tags } = props;
  return (
    <Card className={classes.root}>
      <Link className={classes.a} to={path}>
        <div className={classes.flexBody}>
          <div className={classes.cover}>
            {cover ? (
              <Img sizes={cover} alt={title} />
            ) : (
              <Typography
                className={classes.title}
                variant="title"
              >
                {title}
              </Typography>
            )}
          </div>
          <div className={classes.excerpt}>
            <CardContent className={classes.contentBody}>
              <Typography variant="body1">{excerpt}</Typography>
            </CardContent>
            <CardActions className={classes.tags}>
              <TagArray tags={tags} />
            </CardActions>
          </div>
        </div>

      </Link>
    </Card>
  );
};

PostExcerpt.propTypes = propTypes;

export default withStyles(styles, { name: 'StyledPostFull' })(PostExcerpt);



